import React, { lazy, Suspense } from 'react';
import { Redirect, Route, Switch, useLocation } from 'react-router-dom';
import useUser from './Hooks/useUser';
import Loading from './App/Loading';
import Unauthorized from './Router/401';
import FileNotFound from './Router/404';
import Routes from './Router/Routes.json';
import News from './News/News';
import useScrollUp from './Hooks/useScrollUp';

export default () => {
  
  const
  location = useLocation(),
  user = useUser();

  useScrollUp();

  return (
    <Suspense fallback={Loading()}>
      <Switch location={location}>
        <Route exact path="/" component={News} />
        <Route exact path="/News" render={() => <Redirect to="/" />} />
        <Route exact path="/Auth" render={() => <Redirect to="/" />} />
        <Route exact path="/Auth/Sign-in" component={Loading} />
        <Route exact path="/Auth/Sign-out" component={Loading} />
        <Route exact path="/401" component={Unauthorized} />
        <Route exact path="/404" component={FileNotFound} />{
        
        Routes.map(({ auth, name, pars, path }, key) => {
          const component = lazy(() =>
            import('.' + path).catch((e) => ({ default: FileNotFound }))
          );
          let url = [
            path.substring(
              0,
              path.lastIndexOf('/') + 1
            ) + name.replace(/\s/g, '-')
          ];

          pars && url.push(
            url[0] + '/' + pars.reduce(
              (acc, param) => {
                acc.push(param);
                return acc;
              },
              [] as string[]
            ).join('/')
          );

          return (
            <Route
            exact
            path={url}
            component={(auth && user) || !auth ? component : Unauthorized}
            key={key} />
          );
        })

       }<Redirect to="/404" />
      </Switch>
    </Suspense>
  );
};