import { useCallback, useEffect, useState } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import useToast from './useToast';
import Firebase, { User } from 'firebase/app';
import 'firebase/auth';
import '../Vendor/Firestore';

export default () => {
  const
  [user, setUser] = useState<User | null>(null),
  toast = useToast(),
  history = useHistory(),
  location = useLocation(),

  handleSignIn = useCallback(() => {
    const provider = new Firebase.auth.GoogleAuthProvider();
    Firebase.auth().signInWithPopup(provider)
    .then(result => {
      toast(result.user
        ? 'Sign in successful!'
        : 'Sign in failed!'
      );
      //result.user && log(result.user);
    })
    .catch((e) => {
      //Firestore.log!(e);
      toast(`Error: ${e.code}`);
    });
  }, [toast]),

  handleSignOut = useCallback(() => {
    Firebase.auth().signOut()
    .then(() => toast('Sign out successful!'))
    .catch(e => {
      //Firestore.log!(e);
      toast(`Error: ${e.code}`);
    });
  }, [toast]),
  
  handleLocation = useCallback(() => {

    if (location.pathname === '/Auth/Sign-In' && !user) {
      handleSignIn();
    }
    else if (location.pathname === '/Auth/Sign-Out') {
      handleSignOut();
    }
    else {
      return;
    }
    history.replace('/');
  }, [
    history,
    location.pathname,
    handleSignIn,
    handleSignOut,
    user
  ]);
/*
  log = (user: Firebase.User) => {

    const
    { displayName, email, phoneNumber, uid } = user,
    details = {
      displayName: displayName,
      email: email,
      phoneNumber: phoneNumber,
    },
    login = {
      timestamp: new Date(),
      uid: uid,
    };
  
    return [
      Firestore
        .collection('users')
        .doc(uid)
        .set(details)
        .catch((e) => Firestore.log!(e)),
      Firestore
        .collection('logins')
        .add(login)
        .catch((e) => Firestore.log!(e)),
    ];
  };
*/
  useEffect(() => {
    handleLocation();
  }, [location.pathname, handleLocation]);

  useEffect(() => {
    const unsubscribe = Firebase.auth().onAuthStateChanged(setUser);
    return () => unsubscribe();
  }, []);

  return user;
};